<template>
  <div @click="setTime">
    <div
      ref="clock"
      class="clock"
      :class="[edit ? 'clock-edit' : 'clock-view']"
    >
      <div
        class="clock_minutes_container"
        :class="{ 'clock_minutes_container-view': minutes }"
      >
        <div
          v-for="i of 60"
          :key="i"
          class="clock_minutes"
          :class="{ 'clock_minutes-long': !((i - 1) % 5) }"
          :style="{ transform: 'rotate(' + (i - 1) * 6 + 'deg)' }"
        >
          <div
            class="clock_minutes_number"
            :style="{ transform: 'rotate(' + (i - 1) * -6 + 'deg)' }"
          >
            {{ i - 1 }}
          </div>
        </div>
      </div>
      <div
        v-for="i of 12"
        :key="i"
        class="clock_time"
        :class="[edit ? 'clock_time-edit' : 'clock_time-view']"
        :style="{ transform: 'rotate(' + (i - 1) * 30 + 'deg)' }"
      >
        <div
          class="clock_time_number"
          :style="{ transform: 'rotate(' + (i - 1) * -30 + 'deg)' }"
        >
          {{ ((i + 10) % 12) + 1 }}
        </div>
      </div>
      <div
        class="clock_arm clock_arm-minute"
        :style="{ transform: 'rotate(' + minute * 6 + 'deg)' }"
      ></div>
      <div
        class="clock_arm clock_arm-hour"
        :style="{ transform: 'rotate(' + (hour * 30 + minute / 2) + 'deg)' }"
      ></div>
    </div>
    <v-btn
      v-if="edit"
      :color="minutes ? 'pink' : ''"
      @click.stop="minutes = !minutes"
      class="clock_btn"
      size="x-large"
      >ふん</v-btn
    >
  </div>
</template>

<script>
export default {
  name: 'Clock',
  props: {
    time: {
      type: Array,
      default: () => []
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    minutes: false
  }),
  computed: {
    value: {
      get () {
        return this.time
      },
      set (value) {
        this.$emit('update:time', value)
      }
    },
    hour () {
      return this.time[0] ? this.time[0] : 0
    },
    minute () {
      return this.time[1] ? this.time[1] : 0
    },
    width () {
      return this.$refs.clock.offsetWidth
    }
  },
  methods: {
    setTime (e) {
      const x = e.layerX - this.width / 2
      const y = e.layerY - this.width / 2
      const deg = Math.atan2(y, x) * (180 / Math.PI)
      const timeDiff = Math.round(deg / 30)
      const time = (timeDiff + 15) % 12
      // const currentVal = this.minutes ? this.value[1] : this.value[0]
      if (this.minutes) {
        /* const diff = time * 5 - currentVal
        this.value[1] = diff > 30 ? 60 - diff : time */
        this.value[1] = time * 5
      } else {
        /* const diff = Math.abs(time - currentVal)
        this.value[0] = diff > 6 ? time - 12 : time */
        this.value[0] = time
      }
    }
  }
}
</script>

